import React from 'react'

const WelcomeBar = props => {

    let content = (
        <div className={props.className}>
            <div className="h-8 w-full bg-red-300">
                <h3 className="ticker">hi . hello . welcome . merhaba</h3>
            </div>
        </div>
    )

    return content
}

export default WelcomeBar